import request from '../utils/request';
import { buildUrlWithJsonData } from '@/utils/helpers';
/**
 * @description 获取用户mint数据
 */
export const apiGetMintList = async (param = {}) => {
  const fullUrl = buildUrlWithJsonData(`/api/v1/getMintList`, param);
  const res: any = await request(fullUrl);
  return res;
};

