import React, { useState } from 'react';
import { useDrag } from 'react-dnd';
import './index.css';

interface DraggableProgressProps {
  onProgressChange: (progress: number) => void;
}

const DraggableProgress: React.FC<DraggableProgressProps> = ({
  onProgressChange
}) => {
  const [progress, setProgress] = useState<number>(0);
  const [selectedBlock, setSelectedBlock] = useState<number>(0); // 添加选中状态

  const progressBlocks: number[] = [0, 25, 50, 75, 100];

  const [, dragRef] = useDrag({
    type: 'PROGRESS',
    item: { progress }
  });

  const handleDrop = (index: number): void => {
    const newProgress = progressBlocks[index];
    setProgress(newProgress);
    setSelectedBlock(index); // 更新选中状态
    onProgressChange(newProgress);
  };

  return (
    <div className="progress-container">
      <div
        className="progress-bar"
        style={{ width: `${progress}%`, borderRadius: '20px' }}
      ></div>
      <div className="progress-blocks">
        {progressBlocks.map((block, index) => (
          <div
            key={index}
            className={`progress-block ${
              selectedBlock === index ? 'selected' : ''
            }`}
            onClick={() => handleDrop(index)}
          ></div>
        ))}
      </div>
      <div className="draggable-circle" ref={dragRef}></div>
    </div>
  );
};

export default DraggableProgress;
