import { FC, useState } from 'react';
import DraggableProgress from '../draggableProgress';

interface IProps {
  onProgressChange: (progress: number) => void;
}

const Process: FC<IProps> = (props) => {
  const [currentProgress, setCurrentProgress] = useState<number>(0);

  const handleProgressChange = (progress: number): void => {
    setCurrentProgress(progress);
    props?.onProgressChange(progress);
  };
  return (
    <div className="process-container">
      <div className="text-center font-bold leading-8">
        Additional BRP withdraw
        <span className="text-green">&nbsp;{currentProgress}%</span>
      </div>
      {/* <Progress percent={50} steps={3} strokeColor="#33ee94" showInfo={false} /> */}
      <DraggableProgress onProgressChange={handleProgressChange} />
    </div>
  );
};

export default Process;
