import { useEffect, useState } from 'react';
import { Select, Switch } from 'antd';
import Table from '../table';
import '../../index.css';
import './index.css';
import { apiGetMintTotal } from '@/apis/home';
import { useAppSetting } from '@/store/appSetting';

const RecentRecord = (): JSX.Element => {
  const { auth, setAuth, setClaim, claim } = useAppSetting();
  const [pBRPCount, setpBRPCount] = useState(0);
  const [USDTCount, setUSDTCount] = useState(0);

  const [checked, setChecked] = useState(false);
  const onChange = (checked: boolean) => {
    setChecked(checked);
  };

  const [isMyself, seIsMyself] = useState(false);
  const handleChange = (value: string) => {
    seIsMyself(value === 'Myself');
  };

  // 默认获取 getMintTotal
  const getMintTotal = async () => {
    try {
      let month = '';
      if (checked) {
        month = localStorage.getItem('current_period') || '';
      }
      let address = '';
      if (isMyself) {
        address = auth.address;
      }
      const param = { month, address };
      const { code, message, data } = await apiGetMintTotal(param);
      if (code == 0) {
        const { total_mint, total_claim } = data;
        setpBRPCount(total_mint || 0);
        setUSDTCount(total_claim || 0);
      } else {
        console.log(message);
      }
    } catch (error) {
      console.error(error);
    }
  };

  useEffect(() => {
    if (auth.address) {
      getMintTotal();
    }
  }, [isMyself, checked, auth.address]);

  return (
    <div className="box-container box1-container z-10">
      <div className="box-title-container flex items-center">
        {/* <div className="box-title-container"> */}
        <div className="box-title font-bold flex-auto">Recent Activity</div>
        <div className="switch ml-[40px] mr-[20px] flex-auto">
          <span className="mr-[10px]">Current Period</span>
          <Switch checked={checked} onChange={onChange} />
        </div>
        {/* <div className="select-wrap float-right"> */}
        <Select
          defaultValue="Everyone"
          className="select-filter text-white z-50 flex-auto"
          onChange={handleChange}
          style={{ width: 125 }}
          options={[
            { value: 'Everyone', label: 'Everyone' },
            { value: 'Myself', label: 'Myself' }
          ]}
        />
        {/* </div> */}
      </div>
      <div className="total-count  flex justify-between mt-[10px]">
        <div className="count-item">
          <span className="count-label">Total pBRP Minted:</span>
          <span className="count-value">{pBRPCount}</span>
        </div>
        <div className="count-item text-right">
          <span className="count-label">Total USDT Claimed:</span>
          <span className="count-value">{USDTCount}</span>
        </div>
      </div>
      <Table isMyself={isMyself} isCurrentPeriod={checked} />
    </div>
  );
};

export default RecentRecord;
