import { MerkleTree } from 'merkletreejs';
import { ethers } from 'ethers';

class MTree {
  private tree: MerkleTree | null = null;

  constructor(data: [string, number][]) {
    this.initMerkleTree(data).then(() => {
      console.log('MerkleTree initialized.'); // 可以添加初始化完成后的处理
    });
  }

  private async initMerkleTree(data: [string, number][]): Promise<void> {
    const leaves = await Promise.all(
      data.map((item) =>
        this.keccak256Hash(
          ethers.utils.solidityPack(['address', 'uint256'], [item[0], item[1]])
        )
      )
    );

    this.tree = new MerkleTree(leaves, this.keccak256Hash, { sortPairs: true });
  }

  private async keccak256Hash(input: string): Promise<string> {
    // 使用 Web Crypto API 计算 keccak256 哈希，使用 "SHA-256" 作为算法名称
    const encoder = new TextEncoder();
    const data = encoder.encode(input);

    const hashBuffer = await window.crypto.subtle.digest('SHA-256', data);
    const hashArray = Array.from(new Uint8Array(hashBuffer));
    return ethers.utils.hexlify(hashArray);
  }

  async getRoot(): Promise<string> {
    if (!this.tree) {
      throw new Error('MerkleTree is not initialized yet.');
    }
    return this.tree.getHexRoot();
  }

  async getProof(address: string, amount: number): Promise<string[]> {
    if (!this.tree) {
      throw new Error('MerkleTree is not initialized yet.');
    }

    const leafHex = await this.keccak256Hash(
      ethers.utils.solidityPack(['address', 'uint256'], [address, amount])
    );

    return this.tree.getHexProof(leafHex);
  }
}

export default MTree;
