import React, { useEffect, useState } from 'react';
import InfiniteScroll from 'react-infinite-scroll-component';
import { useAppSetting } from '@/store/appSetting';
import { Avatar, List, Skeleton } from 'antd';
import './index.css';
import {
  address2Avater,
  formatDate,
  openBlankLink,
  shortenString
} from '@/utils/helpers';
import { apiGetMintList } from '@/apis/home';

interface Iprops {
  isMyself: boolean;
  isCurrentPeriod: boolean;
}
interface DataType {
  mint_id: string;
  address: string;
  user_name: string;
  number_mint_pbrp: string;
  number_claim_token: string;
  mint_time: string;
  block_number: number;
  tx: string;
  month: string;
  avater: string;
}

const App: React.FC<Iprops> = ({ isMyself, isCurrentPeriod }) => {
  const { auth, setClaim, claim } = useAppSetting();
  const [page, setPage] = useState(1);
  const [data, setData] = useState<DataType[]>([]);
  const [total, setTotal] = useState(0);
  const [isLoading, setIsLoading] = useState(false);

  // 默认获取 getMintTotal
  const getMintTotalRecord = async (page = 1) => {
    try {
      let month = '';
      if (isCurrentPeriod) {
        month = localStorage.getItem('current_period') || '';
      }
      let address = '';
      if (isMyself) {
        address = auth.address;
      }
      const param = { month, address, page_no: page };
      console.log(param, 'param');
      const { code, message, data } = await apiGetMintList(param);
      if (code == 0) {
        setTotal(data.total || 0);
        return data.list || [];
      } else {
        console.log(message);
        return [];
      }
    } catch (error) {
      console.error(error);
      return [];
    }
  };

  const fetchData = async (pageNum) => {
    try {
      if (isLoading) {
        return;
      }
      setIsLoading(true); // 开始加载时设置 isLoading 为 true
      const newRowData: DataType[] = await getMintTotalRecord(pageNum);
      const newData: DataType[] = [];
      for await (const item of newRowData) {
        item.avater = await address2Avater(item.address);
        newData.push(item);
      }

      // 合并新数据到现有数据
      const _newData = pageNum === 1 ? newData : [...data, ...newData];
      setData(_newData);
      setIsLoading(false); // 加载完成后设置 isLoading 为 false
    } catch (error) {
      console.error('加载数据时发生错误：', error);
      setIsLoading(false); // 如果加载出错，也需要设置 isLoading 为 false
    }
  };

  useEffect(() => {
    setData([]);
    setPage(1);
    if (auth.address) {
      setTimeout(() => {
        fetchData(1);
      }, 100);
    }
  }, [isMyself, isCurrentPeriod, auth.address]);
  // 加载更多数据
  const loadMoreData = () => {
    setPage(page + 1);
    fetchData(page + 1);
  };

  const handleView = (tx: string) => {
    // 检查字符串是否以"http"或"https"开头
    if (tx.startsWith('http://') || tx.startsWith('https://')) {
      openBlankLink(tx);
    } else {
      // 否则，在输入URL前面添加"https://polygonscan.com/tx/"
      openBlankLink(`https://polygonscan.com/tx/${tx}`);
    }
  };

  return (
    <div
      id="scrollableDiv"
      style={{
        height: 540,
        overflow: 'auto'
      }}
    >
      {/* loader={<Skeleton avatar paragraph={{ rows: 2 }} active />} */}
      <InfiniteScroll
        dataLength={data.length}
        next={loadMoreData}
        hasMore={data.length < total}
        loader={<Skeleton avatar paragraph={{ rows: 2 }} active />}
        scrollableTarget="scrollableDiv"
      >
        <List
          dataSource={data}
          renderItem={(item) => (
            <List.Item key={item.mint_id} className="list">
              <div className="text-center">
                {/* src={address2Avater(
                    '0x499405d5c660e71faa42812a2da29971f740c16f'
                  )} */}
                <Avatar src={item.avater} size={28} />
                <p className="address">{shortenString(item.address)}</p>
                <p className="name">
                  {item.mint_id} - {item.user_name}
                </p>
              </div>
              <div className="">
                <p className="pBRP">Mint: {item.number_mint_pbrp} pBRP</p>
                <p className="usdt">Claim: {item.number_claim_token} USDT</p>
              </div>
              <div className="date">{formatDate(item.mint_time)}</div>
              {/* <div className="date">{item.mint_time}</div> */}
              <div className="view" onClick={() => handleView(item.tx)}>
                View
              </div>
            </List.Item>
          )}
        />
      </InfiniteScroll>
    </div>
  );
};

export default App;
