import { useEffect, useState } from 'react';
import { Button } from 'antd';
import Process from '../process';
import '../../index.css';
import './index.css';
import { apiGetSnapshotTime, apiGetTreasuryFunds } from '@/apis/home';
import { useAppSetting } from '@/store/appSetting';
import MTree from '@/utils/merkle';
import { ethers } from 'ethers';
import rewardAbi from '@/abi/reward.json';
import toast from '@/components/toast';

const Benefits = (): JSX.Element => {
  const { auth, setAuth, setClaim, claim } = useAppSetting();

  const [treasuryBrp, setTreasuryBrp] = useState(0);
  const [userBrp, setUserBrp] = useState(0);
  const [treasuryGrant, setTreasuryGrant] = useState(0);
  const [treasuryUsdt, setTreasuryUsdt] = useState(0);

  const getTreasuryFunds = async (param) => {
    try {
      const { code, message, data } = await apiGetTreasuryFunds(param);
      if (code == 0) {
        const {
          user_brp,
          total_treasury_usdt,
          total_treasury_brp,
          total_treasury_grant
        } = data;
        setUserBrp(user_brp);
        setTreasuryBrp(total_treasury_brp);
        setTreasuryGrant(total_treasury_grant);
        setTreasuryUsdt(total_treasury_usdt);
      } else {
        console.log(message);
      }
    } catch (error) {
      console.error(error);
    }
  };

  const [period, setPeriod] = useState('');
  const [snapshot, setSnapshot] = useState('');
  // 默认获取 getSnapshotTime
  const getSnapshotTime = async () => {
    try {
      const { code, message, data } = await apiGetSnapshotTime();
      if (code == 0) {
        const { snapshot_time, current_period } = data;
        await setPeriod(current_period);
        await setSnapshot(snapshot_time);
        localStorage.setItem('current_period', current_period);
        getTreasuryFunds({ address: auth.address, month: current_period });
      } else {
        console.log(message);
      }
    } catch (error) {
      console.error(error);
    }
  };

  useEffect(() => {
    getSnapshotTime();
  }, []);

  const [mintingCount, serMintingCount] = useState(1032);
  const [withdrawCount, setWithdrawCount] = useState(0);

  const [radio, setRadio] = useState<number>(0);

  const handleProgressChange = (progress: number): void => {
    setRadio(progress);
  };

  const getContract = () => {
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const signer = provider.getSigner();
    const contractAddress = import.meta.env.VITE_REWARD_CONTRACT_ADDRESS;
    const contract = new ethers.Contract(contractAddress, rewardAbi, signer);
    return contract;
  };

  const getUserProof = async (address, amount) => {
    const data = [];
    const merkleTree = await new MTree(data);
    const root = await merkleTree.getRoot();
    // console.log(root, 'getRoot()');
    const userProof = merkleTree.getProof(address, amount);
    return userProof;
  };

  const verify = async (address, amount, proof) => {
    try {
      const contract = getContract();
      const flag = await contract.verify(address, amount, proof);
      return flag;
    } catch (error) {
      console.error('Error verifying user eligibility:', error);
      return false;
    }
  };
  const handleClaim = async () => {
    console.log('claim');
    // const loadingToast = toast.loading('claiming...');
    const contract = getContract();
    const amount = ethers.utils.parseUnits(mintingCount.toString(), 18);
    console.log(amount, 'amount');
    const userProof = await getUserProof(auth.address, amount);
    console.log(userProof, 'userProof');
    const isEligible = await verify(auth.address, amount, userProof);
    // if (!isEligible) return false;
    // 调用合约的 claim 函数
    try {
      // 第二个参数 领取的额度 快照的额度
      const tx = await contract.claim(auth.address, amount, userProof, radio);
      await tx.wait();
      console.log('Claim successful!');
      // loadingToast.dismiss();
      toast.success('Claim successful!');

      // 成功之后需要 改变 claim 之后 调取 右边table list 接口
    } catch (error) {
      // loadingToast.dismiss();
      console.error('Claim failed:', error);
      toast.error('Claim failed!');
    }
  };
  return (
    <div className="box-container">
      {/* {contextHolder} */}
      <div className="box-title font-bold">Reputation Benefits</div>
      <div className="date flex justify-between">
        <div>
          <span className="mr-[8px]">Current period:</span>
          <span className="date-value"> {period} </span>
        </div>
        <div>
          <span className="mr-[8px]">Snapshot: </span>
          <span className="date-value">{snapshot}</span>
        </div>
      </div>
      {/* brp 信息 */}
      <div className="infomation text-green size-[16px] text-center columns-2 mt-[40px]">
        <div className="infomation-item mb-[40px]">
          <div className="infomation-label">
            <p>Community-Generated</p>
            <p>Reputation</p>
          </div>
          <div className="infomation-value">{treasuryBrp} BRP</div>
        </div>

        <div className="infomation-item mb-[40px]">
          <div className="infomation-label">Treasury</div>
          <div className="infomation-value">{treasuryGrant} USD</div>
        </div>
        <div className="infomation-item mb-[40px]">
          <div className="infomation-label">
            <p>My Additional</p>
            <p>Reputation</p>
          </div>
          <div className="infomation-value">{userBrp} BRP</div>
        </div>
        <div className="infomation-item mb-[40px]">
          <div className="infomation-label">Treasury Airdrop</div>
          <div className="infomation-value">{treasuryUsdt} USD</div>
        </div>
      </div>

      {/* process */}
      <Process onProgressChange={handleProgressChange} />
      {/* minting */}
      {/* withdraw */}
      <div className="mint-count size-[16px] text-center columns-2 mt-[40px] flex justify-around">
        <div className="mint-count-item">
          <div className="mint-count-label">pBRP Minting</div>
          <div className="mint-count-value">{mintingCount}</div>
        </div>
        <div className="mint-count-item">
          <div className="mint-count-label">Withdrawable USDT</div>
          <div className="mint-count-value">{withdrawCount}</div>
        </div>
      </div>
      <Button
        className={`claim-btn ${radio ? 'active-btn' : ''}`}
        disabled={!radio}
        onClick={handleClaim}
      >
        Claim Reward
      </Button>
    </div>
  );
};

export default Benefits;
